body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
.title {
  background-color: red;
}
.main_dashboard li p:nth-of-type(3) {
  overflow: hidden;
  height: 100px;
}
.main_dashboard img {
  width: 100px;
  /* height: 200px; */
  float: left;
}
.main_dashboard div {
  /* width: 75%; */
  /* float: right; */
}
.main_dashboard li {
  height: auto;
  display: block;
  box-shadow: 0px 0px 5px 0px black;
  padding: 10px;
  margin: 10px;
}
ul.categorie li {
  display: -webkit-box;
  cursor: pointer;
  padding: 15px 0;
}
ul.categorie {
  /* width: 10%; */
  list-style: none;
  float: left;
}
ul.main_dashboard {
  /* width: 60%; */
}
.width {
  display: flex;
  width: 75%;
}
